.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $menu-color;
  font-weight: 600;
  text-transform: uppercase;

  &-top {
    margin-bottom: 24px;

    position: sticky;
    top: 0;
    z-index: 1;
    background-color: $menu-color;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }
  &_phone-number {
    display: block;
    margin-bottom: 16px;
    color: $second-text-color;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }
  &_book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $second-text-color;

    color: $second-text-color;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }

  overflow: auto;
}
